@import "../../../style/theme/default/var.css";

@component-namespace division{
  @b list{
    list-style-type: none;
    padding:0;
    margin:0 0 0 -10px;
    & > li{
      float:left;
      width: 16.666%;
      padding-left: var(--theme-padding-base);
      margin-bottom: var(--theme-margin-base);
        .el-panel__heading {
          height: 30px;
          line-height: 30px;
        }
        .el-panel__heading .el-panel__title  {
          line-height: 30px;
          font-weight: bold;
        }
        .el-panel__body {
          font-size:var(--theme-font-size-small);
          padding: var(--theme-padding-base);
        }
        .control-item {
          margin-bottom: var(--theme-margin-small);
          .control-content i{
            float: right;
            margin-left: 2px;
            line-height: 1.6;
            color: var(--theme-font-color-secondary);
            font-size: var(--theme-font-size-small);
          }
          .control-label {
            width:84px;
            vertical-align: middle;
          }
          .cate-number{
            font-size: var(--theme-font-size-middle);
            position: relative;
            cursor:pointer;
            &.is-refreshing {
              cursor: not-allowed;
            }
            & .cate-shade{
                position: absolute;
                left: 0;
                top: 0;
                display: inline-block;
                width: 100%;
                height: 100%;
                text-align: center;
                background: rgba(0,0,0,0.7);
              & > i{
                animation: rotating 1s linear infinite;
                color: var(--color-white);
                    display: inline-block;

                    vertical-align: top;
              }
            }
          }
        }
    }

    @e footer{
      padding-top: var(--theme-padding-base);
      margin-top: var(--theme-margin-base);
      border-top: 1px solid var(--theme-background-color-primary);
      position:relative;
      padding-right: 30px;
      & .cate-tag{
        max-width: 100%;
        @utils-ellipsis;
        border-color: transparent;
        vertical-align: middle;
        background: transparent;
        padding: 0;
      }
      & .cate-more {
          position:absolute;
          right:0;
          top: 10px;
          padding: 2px 6px 8px 6px;
      }
    }
  }
}

.el-form-item__content {
  .el-form-grid {
    input {
      min-width: 153px;
    }
    .el-input__icon+.el-input__inner {
      padding-right: 16px;
    }
  }
}
.el-form {
  .el-form-item {
    &:last-child {
      .el-form-grid {
        margin-bottom: 0;
      }
    }
    .el-scrollbar {
      margin-bottom: 0;
    }
  }
}
@media (max-width: 1679px) {
  .division-list > li {
    width:20%;
  }
}

@media (max-width: 1368px) {
  .division-list > li {
    width:25%;
  }
}



